.services {
    &__content {
        position: relative;
        padding: 265px 0 320px;
        @include lg {
            padding: 135px 0 148px; }
        @include sm {
            padding: 45px 0; } }
    &__bg {
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        width: 50%;
        overflow: hidden;
        backface-visibility: hidden;
        @include lg {
            width: 52.23%; }
        @include sm {
            display: none; } }
    &__title {
        margin-bottom: 35px;
        @include sm {
            margin-bottom: 10px; } }
    &__header {
        max-width: 400px; }
    &__tabs {
        margin-top: 110px;
        @include sm {
            margin-top: 25px; } }
    &__sphere {
        position: absolute;
        &_lg {
            top: 113px;
            right: 119px;
            @include lg {
                top: 89px;
                right: 100px; } }
        &_sm {
            top: 273px;
            left: 80px;
            @include lg {
                top: 221px;
                left: 124px; } } } }
