.works {
  // background-color: rgba(#FF6F61, 0.2)
  // margin: 227px auto
  padding: 227px 0 227px;
  overflow: hidden;
  &_recent {
 }    // min-height: 3000px
  &__title-wrapper {
    margin-bottom: 110px; }
  &__title {
    position: relative;
    margin-bottom: 28px;
    &_filter {
      span {
        display: inline-block !important; }
      &:after {
        content: '';
        display: inline-block;
        vertical-align: middle;
        @include arr(10, 6, $red, t);
        transition: opacity 0.8s 1s, transform 0.8s;
        opacity: 0;
        .is-animate & {
          opacity: 1; }
        .is-open & {
          transform: rotate(-180deg); } } } }
  &__container {
    position: relative;
    opacity: 0;
    visibility: hidden;
    transition: opacity $tr, visibility $tr;
    margin: 0 -60px;
    @include md {
      margin: 0 -50px; }
    @include sm {
      margin: 0 auto; }
    &.is-init {
      opacity: 1;
      visibility: visible; } }
  &__item {
    width: 50%;
    @include sm {
      width: 100%; }
    &_padding {
      position: absolute;
      right: 0 !important;
      top: 0 !important;
      left: auto !important;
      height: 200px;
      @include md {
        height: 140px; } } }
  &__filter-wrapper {
    margin-bottom: 82px; }
  &__filter {
    display: none;
    margin-top: 87px; }
  &__filter-btn {
    @extend .title;
    background-color: transparent; }
  &__btn[data-anim-text-parent] {
    position: absolute;
    z-index: 5;
    width: 100%;
    left: 0;
    bottom: 0;
    // height: 30px
    display: flex;
    align-items: center;
    padding-left: 60px;
    @include md {
      padding-left: 50px; }
    @include sm {
      padding-left: 0;
      bottom: 0; }
    .btn-to-left & {
      bottom: 117px; }
    .anim-line {
      margin-right: 16px; } } }

.work {
  // width: 20%
  padding-left: 60px;
  padding-right: 60px;
  padding-bottom: 107px;
  @include md {
    padding-bottom: 70px;
    padding-left: 50px;
    padding-right: 50px; }
  @include sm {
    padding-right: 0;
    padding-left: 0; }
  &__image-wrap {
    margin-bottom: 30px;
    overflow: hidden;
    transition: transform $tr;
    background-color: #ccc;
    backface-visibility: hidden;
    @include hover {
      transform: scale(0.97);
      .work__image_scale {
        transform: scale(1.1); } } }
  &__image {
    transition: transform $tr;
    img {
      max-width: 100%;
      // width: 100%
      display: block; } }
  &__title {
    margin-bottom: 8px; } }

.js-masonry-width {
  width: 50%;
  @include sm {
    width: 100%; } }

.js-masonry-stamp {
  position: absolute;
  right: 0;
  top: 0;
  height: 210px;
  width: 40%; }


.filter-controls {
  display: flex;
  align-items: center;
  &__item {
    @include flex-size(20%); } }

.anim-line {
  display: inline-block;
  vertical-align: middle;
  height: 1px;
  width: 40px;
  background-color: $greyMid; }
